.home {
  height: 100vh;
  background-color: #057049;
  display: flex;
  flex-direction: column;
}
.home .sec_1 {
  flex: 4;
  padding: 30px 50px;
}
.home .sec_1 h1 {
  font-size: 75px;
  color: #fff;
}
.home .sec_2 {
  padding: 0 20px;
  flex: 1;
  background-color: #fff;
  background: linear-gradient(
    90deg,
    rgba(4, 14, 37, 1) 0%,
    rgba(1, 14, 45, 1) 100%
  );
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.home .sec_2 div {
  background-color: #08c576;
  width: 250px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
}
